import React from 'react';
import services from '../../services'

export default class PayOutPlan extends React.Component{
  constructor(props){
    super(props);
    this.onLogin = this.onLogin.bind(this)
  }
onLogin(){
  const username = document.getElementById("username").value
  const pass = document.getElementById("pwd").value
  var queryData = {
    username:username,
    password:pass
  }
  services.logincall(queryData,function (response) {
    if(response.authenticated){
      sessionStorage.setItem("username",username)
      sessionStorage.setItem("token",response.token)
      this.props.history.push("/accounts")
    }else {
      console.error("not authourised");
    }
  }.bind(this),function (err) {
    throw(new Error(err.responseText))
  })

}
    render(){
        return(
            <div>
            <div class="row">
                <div class="col-1"></div>
                <div class="col-4">
                 <img src='data:image/png;base64,'
                 alt="Sample App Logo"/>
                </div>
                <div class="col-7"></div>
              </div>

              <div class="row">
  <div class="col-1"></div>
  <div class="col-7"><hr style = {{
marginTop: "1rem",
marginBottom: "1rem",
border: "0",
borderTop: "1px solid rgba(0, 0, 0, 0.1)"
}}/></div>
  <div class="col-2"></div>
</div>
              <div class="row">
              <div class="col-1"></div>
              <div class="col-4">
              <h2>Login</h2>
              <div class="form-group">
                <label for="email">Username</label>
                <input type="email" class="form-control" defaultValue = "alice" id="username"/>
              </div>
              <div class="form-group">
                <label for="pwd">Password </label>
                <input type="password" class="form-control" defaultValue = "wonderland" id="pwd"/>
              </div>
              <div class="form-group form-check">
                <label class="form-check-label">
                  <input class="form-check-input"  type="checkbox"/> Remember me
                </label>
              </div>
              <button type="submit" class="btn btn-primary" onClick = {this.onLogin} >Login</button>
              </div>
              <div class="col-7"></div>
              </div>
              </div>
        );
    }
}



